
.footer {
    background: var(--color-primary-dark);
}

.footer__column {
    display: column;
    gap: 20px;
    align-items: center;
}

.footer__title {
    font-weight: 400;
    color: #fff;
    font-size: 2rem;
    margin-bottom: 30px;
    text-align: center;
}

.footer__icons {
    display: flex;
    justify-content: center;
}

.footer__container-icons {
    display: inline-block;
    width: 50px;
    height: 50px;
    color: #fff;
    text-align: center;
    border: 1px solid #fff;
    border-radius: 50%;
    margin: 0px 10px;
    cursor:pointer
}

.footer__container-icons:hover {
    background: #fff;
    transition: .3s ease-in;
}

.footer__container-icons:hover > .footer__icon {
    color: var(--color-primary-dark);
    transition: .3s ease-in;
}


.footer__icon {
    color: #fff;
    font-size: 25px;
    text-decoration: none;
    cursor: pointer;
}

.footer__contact {
    padding: 40px 0px 10px 0px;
}


.fab.footer__icon {
    line-height: 46px;
}

.footer__made {
    font-size: 1rem;
    font-weight: 700;
    color: #CCD1D1;
    text-align: center;
    margin: 30px 0px;
}

@media (prefers-reduced-motion: no-preference) {
    .react__logo {
        animation: App-logo-spin infinite 20s linear;
        font-size: 1.2rem;
    }
}

@keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  

@media screen and (max-width:800px){


    .footer__grid {
        grid-template-columns: 1fr;
    }

    .footer__contact {
        grid-row: 1/2;
        padding: 30px 0px;
    }
}

@media screen and (max-width:500px){
    .footer__contact {
        padding: 20px 0px;
    }

    .footer__made {
        font-size: .85rem;
    }
}

