.hero {
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    position: relative;
}


.hero .container {
    padding: 0;
}

.nav {
    display: flex;
    justify-content: flex-end;
    height: 70px;
    align-items: center;
    font-weight: 700;
}

.nav--footer {
    font-weight: 300;
    justify-content: flex-start;
}

.nav__items {
    color: #fff;
    text-decoration: none;
    margin-right: 20px;
    padding: 12px 30px;
    font-weight: inherit;
}

.nav__items--cta {
    border: 1px solid #fff;
    border-radius: 5px;
}

.nav__items--footer {
    padding: 10px;
    
}

.hero__container {
    display: flex;
    height: calc(100vh - 70px);
    align-items: center;
    color: #fff;
}

.hero__texts {
    width: 80%;
    margin-bottom: 50px;
}

.hero__title {
    font-size: 3.7rem;
}

.Typewriter span {
    color: #fff;
    font-size: 2rem;
    font-weight: 700;    
}

.Typewriter span.Typewriter__cursor {
    color: var(--color-primary-dark);
}

.hero__subtitle {
    font-size: 2rem;
    font-weight: 300;
    margin: 15px 0;
}

.hero__cta {
    display: inline-block;
    background: transparent;
    padding: 15px 20px;
    color: #fff;
    text-decoration: none;
    border: 2px solid #fff;
    border-radius: 10px;
    font-weight: 600;
    font-size: 1.1rem;
    transition: .3s ease-in;

}

.hero__cta:hover{
    color: var(--color-primary);
    background: #fff;
    transition: .3s ease-in;
}

.hero__wave {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;

}

@media screen and (max-width:800px){
    .nav {
        justify-content: space-around;
   }

   .nav__items--cta {
       border: none;
   }

   .nav__items {
       font-weight: 400;
       border-bottom: 1px solid #fff;
       margin-right: 0;
   }

   .nav__items--footer {
       border: none;
   }

   .hero__texts {
       width: 100%;
       margin-bottom: 80px;
   }

   .hero__title {
       font-size: 2.5rem;
   }

   .Typewriter span {
       color: #fff;
       font-size: 1.4rem;
       font-weight: 700;    
   }

   .hero__subtitle {
       font-size: 1.5rem;
   }
}


@media screen and (max-width:500px){
    .nav {
        flex-wrap: wrap;
        justify-content: space-evenly;
        margin-top: 10px;
    }

    .nav__items {
        padding: 0 10px;
        border: 0;
    }

    .hero__texts {
        text-align: center;
        margin-bottom: 100px;
    }

    .hero__title {
        font-size: 2rem;
    }

    .Typewriter span {
        color: #fff;
        font-size: 1.2rem;
        font-weight: 700;    
    }
}