@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;700&display=swap);
:root{
    --background: linear-gradient(136deg, #21C98B 0%, #1F618D98 100% );
    --color-primary: #21C98B;
    --color-secondary: #21BC83;
    --color-primary-dark: #01353E;
    --boton-padding: 20px 40px;
    --boton-padding-hover: 20px 50px;
}

body {
    font-family: 'Raleway', sans-serif;
}

body::-webkit-scrollbar {
    width: 12px;
}
  
body::-webkit-scrollbar-track {
  background: #EBEDEF;
}
  
body::-webkit-scrollbar-thumb {
  background-color: #21C98B;
  background-color: var(--color-primary);
  border-radius: 40px;
  border: 3px solid #EBEDEF;
}

.container {
    width: 90%;
    margin: 0 auto;
    overflow: hidden;
    padding: 80px 0;
    max-width: 1200px;
}

.subtitle {
    color: #21C98B;
    color: var(--color-primary);
    font-size: 2.5rem;
    margin-bottom: 35px;
}


@media screen and (max-width:800px){
    :root {
        --boton-padding: 18px 40px;
    }

}

@media screen and (max-width:500px){
    :root {
        --boton-padding: 15px 30px;
        --boton-padding-hover: 15px 40px;
    }

    .subtitle {
        font-size: 2rem;
        margin-bottom: 20px;
    }

}
