.hero {
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    position: relative;
}


.hero .container {
    padding: 0;
}

.nav {
    display: flex;
    justify-content: flex-end;
    height: 70px;
    align-items: center;
    font-weight: 700;
}

.nav--footer {
    font-weight: 300;
    justify-content: flex-start;
}

.nav__items {
    color: #fff;
    text-decoration: none;
    margin-right: 20px;
    padding: 12px 30px;
    font-weight: inherit;
}

.nav__items--cta {
    border: 1px solid #fff;
    border-radius: 5px;
}

.nav__items--footer {
    padding: 10px;
    
}

.hero__container {
    display: flex;
    height: calc(100vh - 70px);
    align-items: center;
    color: #fff;
}

.hero__texts {
    width: 80%;
    margin-bottom: 50px;
}

.hero__title {
    font-size: 3.7rem;
}

.Typewriter span {
    color: #fff;
    font-size: 2rem;
    font-weight: 700;    
}

.Typewriter span.Typewriter__cursor {
    color: var(--color-primary-dark);
}

.hero__subtitle {
    font-size: 2rem;
    font-weight: 300;
    margin: 15px 0;
}

.hero__cta {
    display: inline-block;
    background: transparent;
    padding: 15px 20px;
    color: #fff;
    text-decoration: none;
    border: 2px solid #fff;
    border-radius: 10px;
    font-weight: 600;
    font-size: 1.1rem;
    transition: .3s ease-in;

}

.hero__cta:hover{
    color: var(--color-primary);
    background: #fff;
    transition: .3s ease-in;
}

.hero__wave {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;

}

@media screen and (max-width:800px){
    .nav {
        justify-content: space-around;
   }

   .nav__items--cta {
       border: none;
   }

   .nav__items {
       font-weight: 400;
       border-bottom: 1px solid #fff;
       margin-right: 0;
   }

   .nav__items--footer {
       border: none;
   }

   .hero__texts {
       width: 100%;
       margin-bottom: 80px;
   }

   .hero__title {
       font-size: 2.5rem;
   }

   .Typewriter span {
       color: #fff;
       font-size: 1.4rem;
       font-weight: 700;    
   }

   .hero__subtitle {
       font-size: 1.5rem;
   }
}


@media screen and (max-width:500px){
    .nav {
        flex-wrap: wrap;
        justify-content: space-evenly;
        margin-top: 10px;
    }

    .nav__items {
        padding: 0 10px;
        border: 0;
    }

    .hero__texts {
        text-align: center;
        margin-bottom: 100px;
    }

    .hero__title {
        font-size: 2rem;
    }

    .Typewriter span {
        color: #fff;
        font-size: 1.2rem;
        font-weight: 700;    
    }
}

.presentation {
    padding-top: 0;
    text-align: center;
}

.presentation__picture {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin-bottom: 10px;
    object-fit: cover;
    object-position: center;
}

.presentation__copy {
    width: 80%;
    margin: 0 auto;
    font-size: 1.3rem;
}

.presentation__cta{
    display: inline-block;
    margin-top: 30px;
    border: 2px solid var(--color-primary);
    background: #fff;
    color: var(--color-primary);
    text-decoration: none;
    padding: var(--boton-padding);
    border-radius: 10px;
    font-weight: 700;
    font-size: 1.1rem;
    transition: .3s ease-in;
}

.presentation__cta:hover {
    color: #fff;
    background:  var(--color-primary);
    padding: var(--boton-padding-hover);
    transition: .3s ease-in;
}


@media screen and (max-width:800px){
    .presentation__copy {
        width: 100%;
    }

}

@media screen and (max-width:500px){

    .presentation__picture {
        width: 120px;
        height: 120px;
    }

    .presentation__copy {
        font-size: 1rem;
    }
}

.projects {
    background: #f2f2f2;
}

.projects__grid {
    display: grid;
    height: 550px;
    grid-template-areas: 
    "img1 img1 img2 img3"
    "img1 img1 img4 img5";
    grid-gap: 10px;
    gap: 10px;
}

.projects__item {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.projects__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    cursor: pointer;
}

.projects__item:nth-of-type(1){
    grid-area: img1;
}

.projects__item:nth-of-type(2){
    grid-area: img2;
}

.projects__item:nth-of-type(3){
    grid-area: img3;
}

.projects__item:nth-of-type(4){
    grid-area: img4;
}

.projects__item:nth-of-type(5){
    grid-area: img5;
}

.projects__hover {
    position: absolute;
    background: #01353e9f;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: #fff;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transform: translateX(100%);
    cursor: pointer;
    transition: transform .3s ease-in-out;
}

.projects__item:hover .projects__hover {
    transform: translateX(0%);
}

.projects__icon {
    margin-top: 10px;
    font-size: 30px;
}

@media screen and (max-width:800px){
    .projects__grid {
        grid-template-areas: 
        "img1 img1 img2 img2"
        "img1 img1 img3 img3"
        "img4 img4 img5 img5";
    }

}


@media screen and (max-width:500px){
    .projects__grid {
        grid-template-areas: 
        "img1"
        "img2"
        "img3"
        "img4"
        "img5";
        height: auto;
        grid-template-rows: repeat(5, 250px);
        grid-auto-rows: 250px;
        grid-gap: 20px;
        gap: 20px;
    }
}


.stack__grid {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 30px;
    gap: 30px;
}

.stack__item {
    width: 95%;
    margin: 0 auto;
    background: #fff;
    box-shadow: 0px 0px 5px 2px  rgba(66,66,66, .1);
    border-radius: 7px;
    padding: 30px 25px;
    color: var(--color-primary-dark);
    margin-bottom: 50px;
    transition: .3s ease-in;
}


.stack__item:hover {
    background: var(--color-primary); 
    transition: .3s ease-in;
}

.stack__item:hover .stack__person svg {
    color: #fff;
    transition: .3s ease-in;
}

.stack__item:hover .stack__review {
    color: #fff;
    transition: .3s ease-in;
}

.stack__item:hover .stack__texts {
    color: #fff;
    transition: .3s ease-in;
}


.stack__person {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    flex-direction: column;
}

.stack__person svg {
    font-size: 55px;
    margin-bottom: 15px;
}

.stack__img {
    width: 100px;
    min-width: 100px;
    height: 100px;
    object-fit: cover;
    object-position: top;
    border-radius: 50%;
    border: 3px solid #fff;
    margin-right: 30px;
}

.stack__name {
    font-size: 1.35rem;
    margin-bottom: 10px;
    text-align: center;
}

.stack__verification {
    color: #E0AFA0;
    font-weight: 700;
}

.stack__review {
    font-weight: 500;
    text-align: center;
}


@media screen and (max-width:800px){
    .stack__grid {
        grid-template-columns: 1fr 1fr;
        grid-gap: 30px;
        gap: 30px;
    }

    .stack__item {
        margin-bottom: 30px;
    }
}

@media screen and (max-width:550px){

    .stack__grid {
        grid-template-columns: 1fr;
        grid-gap: 30px;
        gap: 30px;
    }

    .stack__item {
        padding: 30px 15px;
    }

    .stack__texts {
        text-align: center;
    }

    .stack__person {
        flex-direction: column;
    }

    .stack__img {
        margin:0;
        margin-bottom: 20px;
    }
}

.footer {
    background: var(--color-primary-dark);
}

.footer__column {
    display: column;
    grid-gap: 20px;
    gap: 20px;
    align-items: center;
}

.footer__title {
    font-weight: 400;
    color: #fff;
    font-size: 2rem;
    margin-bottom: 30px;
    text-align: center;
}

.footer__icons {
    display: flex;
    justify-content: center;
}

.footer__container-icons {
    display: inline-block;
    width: 50px;
    height: 50px;
    color: #fff;
    text-align: center;
    border: 1px solid #fff;
    border-radius: 50%;
    margin: 0px 10px;
    cursor:pointer
}

.footer__container-icons:hover {
    background: #fff;
    transition: .3s ease-in;
}

.footer__container-icons:hover > .footer__icon {
    color: var(--color-primary-dark);
    transition: .3s ease-in;
}


.footer__icon {
    color: #fff;
    font-size: 25px;
    text-decoration: none;
    cursor: pointer;
}

.footer__contact {
    padding: 40px 0px 10px 0px;
}


.fab.footer__icon {
    line-height: 46px;
}

.footer__made {
    font-size: 1rem;
    font-weight: 700;
    color: #CCD1D1;
    text-align: center;
    margin: 30px 0px;
}

@media (prefers-reduced-motion: no-preference) {
    .react__logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
        font-size: 1.2rem;
    }
}

@-webkit-keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

@keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  

@media screen and (max-width:800px){


    .footer__grid {
        grid-template-columns: 1fr;
    }

    .footer__contact {
        grid-row: 1/2;
        padding: 30px 0px;
    }
}

@media screen and (max-width:500px){
    .footer__contact {
        padding: 20px 0px;
    }

    .footer__made {
        font-size: .85rem;
    }
}



.skills {
    min-height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 80px;
    row-gap: 80px;
    justify-items: center;
    align-items: center;
}

.skills__img {
     text-align: center;
}

.skills__img--left {
    text-align: left;
}

.skills__title {
    color: #fff;
    font-size: 2.5rem;
    margin-bottom: 20px;
}

.skills__picture {
    max-width: 80%;
}

.skills__paragraph {
    margin-bottom: 20px;
    line-height: 1.5;
    font-weight: 300;
}

.skills__description {
    height: 100%;
    width: 100%;
    padding: 10% 10%;
    background: var(--color-primary);
}

.skills__description_0{
    height: 100%;
    width: 100%;
    padding: 10% 10%;

}

.skills__progress {
    width: 80%;
    margin-bottom: 30px;
}

.progress__info {
    background: yellow;
    display: inline;
    grid-template-rows: 1fr 1fr;
    flex-direction: row;
    justify-content: space-between;
}

.progress__percent {
    float: right;
}

.progress__bar{
    margin: 10px 0px;
}

.progress__name, .progress__percent {
    font-size: 1.3rem;
    font-weight: 500;
    color:#fff;
}


@media screen and (max-width:800px){
    .skills{ 
        grid-template-columns: 1fr;
    }

    .skills.container {
        padding-top: 30px;
    }

    .skills__img--left {
        text-align: center;
    }

    .skills__texts:last-child {
        grid-row: 3/4;
    }

    .skills__progress {
        width: 90%;
    }
}

@media screen and (max-width:500px){
    .skills {
        grid-row-gap: 60px;
        row-gap: 60px;
    }

    .skills.container {
        padding-top: 10px;
    }

    .skills__progress {
        width: 100%;
    }

    .progress__name, .progress__percent {
        font-size: 1.1rem;
        font-weight: 50H10;
        color:#fff;
    }
    
}
.not_found {
    height: 100vh;
    width: 100%;
    justify-items: center;
    align-items: center;
    justify-content: center;
    display: block;
}

.not_found__detail{
    position: fixed; /* or absolute */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: grid;
    justify-content: center;
    align-content: center;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 80px;
    row-gap: 80px;
}

.not_found__title{
    font-size: 3rem;
}

.not_found__info {
    width: 100%;
    justify-content: center;
    justify-items: center;
}

.not_found__img{
    width: 100%;
    text-align: center;
    justify-content: center;
}

.not_found__picture{
    max-width: 80%;
}

.not_found__cta{
    display: inline-block;
    margin-top: 30px;
    background: var(--color-primary);
    color: #fff;
    text-decoration: none;
    padding: 20px 45px;
    border-radius: 10px;
    font-weight: 700;
    font-size: 1.3rem;
    transition: .3s ease-in;
}

.not_found__cta:hover {
    background: #17A470;
    transition: .3s ease-in;
}

@media screen and (max-width:800px){
    .not_found__title{
        font-size: 2rem;
    }
    .not_found__picture{
        max-width: 80%;
    }

    .not_found__cta{
        padding: 15px 30px;
        font-size: 1rem;
    }
}

@media screen and (max-width:550px){
   
    .not_found__detail{
