
.skills {
    min-height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 80px;
    justify-items: center;
    align-items: center;
}

.skills__img {
     text-align: center;
}

.skills__img--left {
    text-align: left;
}

.skills__title {
    color: #fff;
    font-size: 2.5rem;
    margin-bottom: 20px;
}

.skills__picture {
    max-width: 80%;
}

.skills__paragraph {
    margin-bottom: 20px;
    line-height: 1.5;
    font-weight: 300;
}

.skills__description {
    height: 100%;
    width: 100%;
    padding: 10% 10%;
    background: var(--color-primary);
}

.skills__description_0{
    height: 100%;
    width: 100%;
    padding: 10% 10%;

}

.skills__progress {
    width: 80%;
    margin-bottom: 30px;
}

.progress__info {
    background: yellow;
    display: inline;
    grid-template-rows: 1fr 1fr;
    flex-direction: row;
    justify-content: space-between;
}

.progress__percent {
    float: right;
}

.progress__bar{
    margin: 10px 0px;
}

.progress__name, .progress__percent {
    font-size: 1.3rem;
    font-weight: 500;
    color:#fff;
}


@media screen and (max-width:800px){
    .skills{ 
        grid-template-columns: 1fr;
    }

    .skills.container {
        padding-top: 30px;
    }

    .skills__img--left {
        text-align: center;
    }

    .skills__texts:last-child {
        grid-row: 3/4;
    }

    .skills__progress {
        width: 90%;
    }
}

@media screen and (max-width:500px){
    .skills {
        row-gap: 60px;
    }

    .skills.container {
        padding-top: 10px;
    }

    .skills__progress {
        width: 100%;
    }

    .progress__name, .progress__percent {
        font-size: 1.1rem;
        font-weight: 50H10;
        color:#fff;
    }
    
}