.not_found {
    height: 100vh;
    width: 100%;
    justify-items: center;
    align-items: center;
    justify-content: center;
    display: block;
}

.not_found__detail{
    position: fixed; /* or absolute */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: grid;
    justify-content: center;
    align-content: center;
    grid-template-columns: 1fr 1fr;
    row-gap: 80px;
}

.not_found__title{
    font-size: 3rem;
}

.not_found__info {
    width: 100%;
    justify-content: center;
    justify-items: center;
}

.not_found__img{
    width: 100%;
    text-align: center;
    justify-content: center;
}

.not_found__picture{
    max-width: 80%;
}

.not_found__cta{
    display: inline-block;
    margin-top: 30px;
    background: var(--color-primary);
    color: #fff;
    text-decoration: none;
    padding: 20px 45px;
    border-radius: 10px;
    font-weight: 700;
    font-size: 1.3rem;
    transition: .3s ease-in;
}

.not_found__cta:hover {
    background: #17A470;
    transition: .3s ease-in;
}

@media screen and (max-width:800px){
    .not_found__title{
        font-size: 2rem;
    }
    .not_found__picture{
        max-width: 80%;
    }

    .not_found__cta{
        padding: 15px 30px;
        font-size: 1rem;
    }
}

@media screen and (max-width:550px){
   
    .not_found__detail{
        grid-template-columns: 1fr;
    }

    .not_found__title{
        font-size: 2.2rem;
    }
    .not_found__picture{
        max-width: 100%;
    }
    .not_found__info{
        text-align: center;
    }

    .not_found__cta{
        padding: 13px 25px;
        font-size: 1rem;
    }
}