
.stack__grid {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
}

.stack__item {
    width: 95%;
    margin: 0 auto;
    background: #fff;
    box-shadow: 0px 0px 5px 2px  rgba(66,66,66, .1);
    border-radius: 7px;
    padding: 30px 25px;
    color: var(--color-primary-dark);
    margin-bottom: 50px;
    transition: .3s ease-in;
}


.stack__item:hover {
    background: var(--color-primary); 
    transition: .3s ease-in;
}

.stack__item:hover .stack__person svg {
    color: #fff;
    transition: .3s ease-in;
}

.stack__item:hover .stack__review {
    color: #fff;
    transition: .3s ease-in;
}

.stack__item:hover .stack__texts {
    color: #fff;
    transition: .3s ease-in;
}


.stack__person {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    flex-direction: column;
}

.stack__person svg {
    font-size: 55px;
    margin-bottom: 15px;
}

.stack__img {
    width: 100px;
    min-width: 100px;
    height: 100px;
    object-fit: cover;
    object-position: top;
    border-radius: 50%;
    border: 3px solid #fff;
    margin-right: 30px;
}

.stack__name {
    font-size: 1.35rem;
    margin-bottom: 10px;
    text-align: center;
}

.stack__verification {
    color: #E0AFA0;
    font-weight: 700;
}

.stack__review {
    font-weight: 500;
    text-align: center;
}


@media screen and (max-width:800px){
    .stack__grid {
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }

    .stack__item {
        margin-bottom: 30px;
    }
}

@media screen and (max-width:550px){

    .stack__grid {
        grid-template-columns: 1fr;
        gap: 30px;
    }

    .stack__item {
        padding: 30px 15px;
    }

    .stack__texts {
        text-align: center;
    }

    .stack__person {
        flex-direction: column;
    }

    .stack__img {
        margin:0;
        margin-bottom: 20px;
    }
}