
.presentation {
    padding-top: 0;
    text-align: center;
}

.presentation__picture {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin-bottom: 10px;
    object-fit: cover;
    object-position: center;
}

.presentation__copy {
    width: 80%;
    margin: 0 auto;
    font-size: 1.3rem;
}

.presentation__cta{
    display: inline-block;
    margin-top: 30px;
    border: 2px solid var(--color-primary);
    background: #fff;
    color: var(--color-primary);
    text-decoration: none;
    padding: var(--boton-padding);
    border-radius: 10px;
    font-weight: 700;
    font-size: 1.1rem;
    transition: .3s ease-in;
}

.presentation__cta:hover {
    color: #fff;
    background:  var(--color-primary);
    padding: var(--boton-padding-hover);
    transition: .3s ease-in;
}


@media screen and (max-width:800px){
    .presentation__copy {
        width: 100%;
    }

}

@media screen and (max-width:500px){

    .presentation__picture {
        width: 120px;
        height: 120px;
    }

    .presentation__copy {
        font-size: 1rem;
    }
}